@import url("https://fonts.googleapis.com/css?family=Chewy|Gloria+Hallelujah");
@import url("https://fonts.googleapis.com/css?family=Questrial&display=swap");
@import url("https://fonts.googleapis.com/css?family=Chewy|Gloria+Hallelujah");
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);
:root {
  --dark-mode: #131418;
  --normal-mode: #ffffff;
  --dark-card: #13141f;
  --normal-card: #ffffff;
  --main-background-color: #ffffff;
  --semi-background-color: #f5f6f8;
  --card-background-color: #ffffff;
  --default-fontcolor: #000000;
  --default-titles: #000000;
  --responsive-headline-color: #000000; }

.toggle-dark {
  position: absolute;
  top: 1vh;
  right: 1vh; }

body {
  background: var(--main-background-color);
  font-size: 15px;
  font-weight: 500;
  color: var(--default-fontcolor); }

section {
  margin: 8vh 0 8vh 0px; }

.card {
  position: relative;
  display: flex;
  min-width: 0;
  word-wrap: break-word;
  background-clip: initial;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 1.2rem;
  background-color: var(--card-background-color);
  border: none;
  box-shadow: 0 0.46875rem 2.1875rem rgba(90, 97, 105, 0.1), 0 0.9375rem 1.40625rem rgba(90, 97, 105, 0.1), 0 0.25rem 0.53125rem rgba(90, 97, 105, 0.12), 0 0.125rem 0.1875rem rgba(90, 97, 105, 0.1); }

/*
Flex derection of the contents of the row
*/
.card .flex-row {
  flex-direction: row; }

.flex-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }

.card .flex-col {
  flex-direction: column; }

.profile-pic {
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 25%; }

/* Titles */
hr {
  margin-top: 1vh;
  margin-left: 0;
  border: 0;
  height: 4px;
  background-image: -webkit-gradient(linear, left top, right top, from(#1180ff), to(#f14bf0));
  background-image: linear-gradient(to right, #00466c, #1e90ff);
  border-radius: 9px;
  width: 70%;
  z-index: -1; }
  hr.center {
    margin-left: auto; }

.titles {
  display: inline-block;
  color: var(--default-titles); }

h3.dark,
h4.dark,
span.dark {
  background: linear-gradient(to right, #0065a2 0%, #1e90ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

