.social-container {
  max-width: 600px;
  margin: 0px auto;
  text-align: center; }

.social-icons {
  padding: 0;
  list-style: none;
  margin: 1em; }

.social-icons li {
  display: inline-block;
  margin: 0.15em;
  position: relative;
  font-size: 1.2em; }

.social-icons i {
  color: #fff;
  position: absolute;
  top: 21px;
  left: 21px;
  transition: all 265ms ease-out; }

.social-icons a {
  display: inline-block; }

.social-icons a:before {
  transform: scale(1);
  -ms-transform: scale(1);
  -webkit-transform: scale(1);
  content: " ";
  width: 60px;
  height: 60px;
  border-radius: 100%;
  display: block;
  background: linear-gradient(45deg, #1180ff, #73b9df);
  transition: all 265ms ease-out; }

.social-icons a:hover:before {
  transform: scale(0);
  transition: all 265ms ease-in; }

.social-icons a:hover i {
  transform: scale(2.2);
  -ms-transform: scale(2.2);
  -webkit-transform: scale(2.2);
  color: #00b5f5;
  background: -webkit-linear-gradient(45deg, #00b5f5, #2256be);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 265ms ease-in; }

#my-wave {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  bottom: -5%; }

#front-content {
  z-index: 1; }

.profile-pic {
  position: relative;
  width: 250px;
  height: 250px;
  border-radius: 25%; }

.circle {
  position: relative;
  width: 120px;
  height: 120px; }

.circle img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 75px;
  border-radius: 50%; }

.circle svg {
  fill: none;
  stroke: #8a3ab9;
  stroke-linecap: round;
  stroke-width: 3;
  stroke-dasharray: 1;
  stroke-dashoffset: 0;
  animation: stroke-draw 6s ease-out infinite alternate;
  /* // animation: stroke-draw 6s cubic-bezier(0.77, 0, 0.175, 1) infinite alternate;  */ }

@keyframes stroke-draw {
  from {
    /* Strokes around the the image */
    stroke: #8a3ab9;
    stroke-dasharray: 1; }
  to {
    /* stroke:#cd486b; */
    transform: rotate(180deg);
    stroke-dasharray: 8; } }

